import React, {Component} from 'react';

import Name from './component/Name';
import Introduction from './component/Introduction';
import Timeline from './component/Timeline';
import Project from "./component/Project";
import Other from "./component/Other";
import Photography from "./component/Photography";
import Map_America from "./component/Map_America";
import Map_Asia from "./component/Map_Asia";

import Particles from 'react-particles-js';

import './component/Component.css';
import './App.css';

import Nao from './file/Nao.JPG';
import Zhengzhou from './file/Zhengzhou.jpeg';
import Yue from './file/Yue.jpg';
import Umass from './file/Umass.jpg';
import UW from './file/UW.jpg';
import Style from './file/Style.png';
import Help from './file/Help.png';
import Madison from './file/Madison.jpg';

class App extends Component {
    constructor(props) {
        super(props);
        this.Nao_mouseOver = this.Nao_mouseOver.bind(this);
        this.Nao_mouseOut = this.Nao_mouseOut.bind(this);
        this.Z_mouseOver = this.Z_mouseOver.bind(this);
        this.Z_mouseOut = this.Z_mouseOut.bind(this);
        this.Y_mouseOver = this.Y_mouseOver.bind(this);
        this.Y_mouseOut = this.Y_mouseOut.bind(this);
        this.Umass_mouseOver = this.Umass_mouseOver.bind(this);
        this.Umass_mouseOut = this.Umass_mouseOut.bind(this);
        this.UW_mouseOver = this.UW_mouseOver.bind(this);
        this.UW_mouseOut = this.UW_mouseOut.bind(this);
        this.Style_mouseOver = this.Style_mouseOver.bind(this);
        this.Style_mouseOut = this.Style_mouseOut.bind(this);
        this.Help_mouseOver = this.Help_mouseOver.bind(this);
        this.Help_mouseOut = this.Help_mouseOut.bind(this);
        this.M_mouseOver = this.M_mouseOver.bind(this);
        this.M_mouseOut = this.M_mouseOut.bind(this);
        this.Slide_mouseOver = this.Slide_mouseOver.bind(this);
        this.Slide_mouseOut = this.Slide_mouseOut.bind(this);
        this.Map_mouseOver = this.Map_mouseOver.bind(this);
        this.Map_mouseOut = this.Map_mouseOut.bind(this);
        this.state = {
            Nao_display: false,
            Zhengzhou_display: false,
            Yue_display: false,
            Umass_display: false,
            UW_display: false,
            Style_display: false,
            Help_display: false,
            Madison_display: false,
            Slide_display: false,
            Map_display: false,
        };
    }

    Nao_mouseOver() {
        this.setState({
            Nao_display: true
        })
    }

    Nao_mouseOut() {
        this.setState({
            Nao_display: false
        })
    }

    Z_mouseOver() {
        this.setState({
            Zhengzhou_display: true
        })
    }

    Z_mouseOut() {
        this.setState({
            Zhengzhou_display: false
        })
    }

    Y_mouseOver() {
        this.setState({
            Yue_display: true
        })
    }

    Y_mouseOut() {
        this.setState({
            Yue_display: false
        })
    }

    Umass_mouseOver() {
        this.setState({
            Umass_display: true
        })
    }

    Umass_mouseOut() {
        this.setState({
            Umass_display: false
        })
    }

    UW_mouseOver() {
        this.setState({
            UW_display: true
        })
    }

    UW_mouseOut() {
        this.setState({
            UW_display: false
        })
    }

    Style_mouseOver() {
        this.setState({
            Style_display: true
        })
    }

    Style_mouseOut() {
        this.setState({
            Style_display: false
        })
    }

    Help_mouseOver() {
        this.setState({
            Help_display: true
        })
    }

    Help_mouseOut() {
        this.setState({
            Help_display: false
        })
    }

    M_mouseOver() {
        this.setState({
            Madison_display: true
        })
    }

    M_mouseOut() {
        this.setState({
            Madison_display: false
        })
    }

    Slide_mouseOver() {
        this.setState({
            Slide_display: true
        })
    }

    Slide_mouseOut() {
        this.setState({
            Slide_display: false
        })
    }

    Map_mouseOver() {
        this.setState({
            Map_display: true
        })
    }

    Map_mouseOut() {
        this.setState({
            Map_display: false
        })
    }

    render() {
        return (
            <div>
                <div className="App">
                    <div className="Left">
                        <Name mouseOver={this.Y_mouseOver} mouseOut={this.Y_mouseOut}/>
                        <Introduction
                            mouseOver_Z={this.Z_mouseOver} mouseOut_Z={this.Z_mouseOut}
                            mouseOver_Madison={this.M_mouseOver} mouseOut_Madison={this.M_mouseOut}
                        />
                        <Timeline
                            mouseOver_HCI={this.Nao_mouseOver} mouseOut_HCI={this.Nao_mouseOut}
                            mouseOver_Umass={this.Umass_mouseOver} mouseOut_Umass={this.Umass_mouseOut}
                            mouseOver_UW={this.UW_mouseOver} mouseOut_UW={this.UW_mouseOut}
                        />
                        <Project
                            mouseOver_Style={this.Style_mouseOver} mouseOut_Style={this.Style_mouseOut}
                            mouseOver_Help={this.Help_mouseOver} mouseOut_Help={this.Help_mouseOut}
                        />
                        <Other
                            mouseOver_Slide={this.Slide_mouseOver} mouseOut_Slide={this.Slide_mouseOut}
                            mouseOver_Map={this.Map_mouseOver} mouseOut_Map={this.Map_mouseOut}
                        />
                    </div>

                    <div className="Right">
                        {this.state.Map_display ? (
                            <div className="map">
                                <div className="map_title">
                                    Where I've been in the US
                                </div>
                                <Map_America/>
                                <br/>
                                <div className="map_title">
                                    Where I've been in China
                                </div>
                                <Map_Asia/>
                            </div>
                        ) : null}
                        {this.state.Slide_display ? <Photography className="image_frame"/> : null}
                        {this.state.Yue_display ? (<img className="image_high" alt="" src={Yue}/>) : null}
                        {this.state.Zhengzhou_display ? (<img className="image_wide" alt="" src={Zhengzhou}/>) : null}
                        {this.state.Nao_display ? (<img className="image_high" alt="" src={Nao}/>) : null}
                        {this.state.Umass_display ? (<img className="image_wide" alt="" src={Umass}/>) : null}
                        {this.state.UW_display ? (<img className="image_wide" alt="" src={UW}/>) : null}
                        {this.state.Style_display ? (<img className="image_high" alt="" src={Style}/>) : null}
                        {this.state.Help_display ? (<img className="image_wide" alt="" src={Help}/>) : null}
                        {this.state.Madison_display ? (<img className="image_wide" alt="" src={Madison}/>) : null}
                    </div>
                </div>
                <Particles
                    params={{
                        "particles": {
                            "number": {
                                "value": 60,
                                "density": {
                                    "enable": true,
                                    "value_area": 1500
                                }
                            },
                            "line_linked": {
                                "enable": true,
                                "opacity": 0.02
                            },
                            "move": {
                                "direction": "right",
                                "speed": 0.1
                            },
                            "size": {
                                "value": 1
                            },
                            "opacity": {
                                "anim": {
                                    "enable": true,
                                    "speed": 1,
                                    "opacity_min": 0.05
                                }
                            }
                        },
                        "interactivity": {
                            "events": {
                                "onclick": {
                                    "enable": true,
                                    "mode": "push"
                                }
                            },
                            "modes": {
                                "push": {
                                    "particles_nb": 1
                                }
                            }
                        },
                        "retina_detect": true
                    }} />
            </div>
        );
    }
}

export default App;
